<template>
    <section style="margin-left:15px;margin-right:15px;margin-top: 15px;">
      <el-divider></el-divider>
      <el-descriptions class="margin-top" title="" :column="3" :size="size">
        <template slot="title">
        <span>{{ featureMeta.feature_cn }}</span>
        </template>
      <template slot="extra">
        <router-link :to="{ name: '特征存储',
        params: {feature_id: this.$route.params.feature_id}}"
        style="text-decoration:none;color: aliceblue">
        <el-button class="return-list"><i class="el-icon-arrow-left" style="margin-right: 5px"></i>返回特征列表</el-button>
        </router-link>
      </template>
      <el-descriptions-item label="特征ID">{{ featureMeta.feature_en }}</el-descriptions-item>
      <el-descriptions-item label="创建时间"><timeago :datetime="featureMeta.created_at" locale="zh-CN"></timeago></el-descriptions-item>
      <el-descriptions-item label="最近更新"><timeago :datetime="featureMeta.updated_at" locale="zh-CN"></timeago></el-descriptions-item>

      <el-descriptions-item v-if="featureMeta.status==='production'" label="特征状态"><el-tag size="small">生产</el-tag></el-descriptions-item>
      <el-descriptions-item v-else-if="featureMeta.status==='dev'" label="特征状态"><el-tag size="small" type="warning">开发</el-tag></el-descriptions-item>
      <el-descriptions-item v-else label="特征状态"><el-tag size="small" type="info">已下线</el-tag></el-descriptions-item>
    </el-descriptions>

    <el-collapse v-model="activeNames" @change="handleChange">
    <el-collapse-item title="特征描述" name="1">
      <span v-show="descvisalble">{{ featureMeta.description }}&nbsp;&nbsp;</span>
      <el-button v-show="descvisalble" type="text" @click="handleEdit"><i class="el-icon-edit"></i></el-button>

      <el-input v-show="descEditable" type="textarea" style="width:60%" :rows="3" v-model="editFeatureMeta.description"></el-input>
      <div>
      <el-button type="text"  v-show="descEditable" @click="handelCancel" style="margin-right: 0px;" icon="el-icon-arrow-left">取消</el-button>
      <el-button style="align:right" v-show="descEditable" type="text" icon="el-icon-check" @click="submitSave" circle >保存</el-button>
    </div>
    </el-collapse-item>
    <el-collapse-item title="使用该特征的模型" name="2">
      <!--工具条-->
      <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
            <el-form :inline="true" :model="filters" @submit.native.prevent>
                <el-form-item>
                    <el-input v-model="filters.name" placeholder="名称" @keyup.enter.native="getFeatureModelList"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="getFeatureModelList">查询</el-button>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="handleAdd" icon="el-icon-plus">已注册的模型</el-button>
                </el-form-item>
            </el-form>
        </el-col>
      <el-table :data="ModelList" highlight-current-row
                  v-loading="listLoading"
                  @expand-change="expandModelClick"
                  style="width: 100%;">
            <el-table-column type="index" width="55">
            </el-table-column>
            <el-table-column prop="name" label="模型名称" min-width="20%" sortable show-overflow-tooltip>
                <template slot-scope="scope">
                    <el-icon name="name"></el-icon>
                    <router-link :to="{ name: '模型版本管理', params: {model_name: scope.row.name}}" style="cursor:pointer;color: #0000FF;text-decoration:underline">{{ scope.row.name }}
                    </router-link>
                </template>
            </el-table-column>
            <el-table-column prop="version" label="模型版本" min-width="15%" sortable show-overflow-tooltip>
            </el-table-column>
            <el-table-column prop="status" label="模型状态" min-width="15%" sortable show-overflow-tooltip>
                <template slot-scope="scope">
                    <span v-if="scope.row.status==='production'" label="特征状态"><el-tag size="small">生产</el-tag></span>
                    <span v-else-if="scope.row.status==='dev'" label="特征状态"><el-tag size="small" type="warning">开发</el-tag></span>
                    <span v-else label="特征状态"><el-tag size="small" type="info">已下线</el-tag></span>
                </template>
            </el-table-column>
            <el-table-column prop="created_at" label="创建时间" min-width="15%" sortable show-overflow-tooltip>
                <template slot-scope="scope">
                    <span style="font-size: 16px;"><timeago :datetime="scope.row.created_at" locale="zh-CN"></timeago></span>
                </template>
            </el-table-column>

            <el-table-column prop="description" label="特征使用详情" min-width="20%" sortable>
                <template slot-scope="scope">
                    <span style="font-size: 16px;">{{scope.row.description}}</span>
                </template>
            </el-table-column>

            <el-table-column label="操作" min-width="15%" align="right">
                <template slot-scope="scope">
                    <el-button type="primary" style="text-align:right" size="small" @click="handleSubModelEdit(scope.$index, scope.row)">编辑</el-button>
                    <el-button type="danger" style="text-align:right" size="small" @click="handleDel(scope.$index, scope.row)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <el-col :span="24" class="toolbar">
            <el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="20" :page-count="total" style="float:right;">
            </el-pagination>
        </el-col>
    </el-collapse-item>
  </el-collapse>
  <!--编辑界面-->
  <el-dialog title="编辑模型信息" :visible.sync="editFormVisible" :close-on-click-modal="false" style="width: 85%; left: 12.5%">
            <el-form :model="editForm" label-width="80px"  :rules="editFormRules" ref="editForm">
                <el-form-item label="模型名称" prop="name">
                    <el-input v-model.trim="editForm.name" auto-complete="off" disabled></el-input>
                </el-form-item>
                <el-row :gutter="24">
                    <el-col :span="12">
                        <el-form-item label="模型版本" prop="version">
                            <el-input v-model.trim="editForm.version" auto-complete="off" disabled></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="使用状态" prop="status">
                            <el-select v-model="editForm.status" placeholder="请选择">
                                <el-option v-for="item in StatusOptions" :key="item.value" :label="item.label" :value="item.value">
                                </el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-form-item label="使用详情" prop='description'>
                    <el-input type="textarea" :rows="3" v-model="editForm.description"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="editFormVisible = false">取消</el-button>
                <el-button type="primary" @click.native="editSubmit" :loading="editLoading">提交</el-button>
            </div>
        </el-dialog>
  <!--新增界面-->
  <el-dialog title="新增模型信息" :visible.sync="addFormVisible" :close-on-click-modal="false" style="width: 75%; left: 12.5%">
            <el-form :model="addForm" label-width="80px" :rules="addFormRules" ref="addForm">
                <el-row :gutter="24">
                    <el-col :span="24">
                <el-form-item label="模型名称" prop="name">
                    <el-autocomplete v-model.trim="addForm.name"
                    style="width:100%"
                    class="inline-input"
                    :fetch-suggestions="querySearchAsync"
                    @select="handleSelect">
                    </el-autocomplete>
                </el-form-item>
            </el-col>
            </el-row>
                <el-form-item label="模型版本" prop="version">
                    <el-input v-model.trim="addForm.version" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="使用详情" prop='description'>
                    <el-input type="textarea" :rows="3" v-model="addForm.description"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="addFormVisible = false">取消</el-button>
                <el-button type="primary" @click.native="addSubmit" :loading="addLoading">提交</el-button>
            </div>
        </el-dialog>

    </section>
  </template>

<script>
// {{moment(scope.row.updateTime).format("YYYY-MM-DD HH:mm:ss")}}
import { test, addFeatureModel, EditFeatureDesc, updateFeatureModel, getRegisteredModels } from '@/api/api'
import $ from 'jquery'
import moment from 'moment'
export default {
  data () {
    return {
      state: '',
      models: [],
      timeout: null,
      StatusOptions: [{ label: '生产', value: 'production' },
        { label: '开发', value: 'dev' },
        { label: '已下线', value: 'archive' }],
      // 编辑界面数据
      editForm: {
        name: '',
        version: '',
        status: '',
        description: ''
      },
      editFormVisible: false,
      descvisalble: true,
      descEditable: false,
      featureMeta: {},
      editFeatureMeta: {
        description: ''
      },
      addFormVisible: false, // 新增界面是否显示
      addLoading: false,
      addFormRules: {
        name: [
          { required: true, message: '请输入名称', trigger: 'select' },
          { min: 1, max: 50, message: '长度在 1 到 50 个字符', trigger: 'input' }
        ],
        version: [
          { required: true, message: '请输入版本', trigger: 'blur' },
          { min: 1, max: 50, message: '长度在 1 到 50 个字符', trigger: 'blur' }
        ],
        description: [
          { required: false, message: '请输入版本号', trigger: 'blur' },
          { max: 1024, message: '不能超过1024个字符', trigger: 'blur' }
        ]
      },
      // 新增界面数据
      addForm: {
        name: '',
        version: '',
        description: ''
      },
      activeNames: ['1', '2'],
      firstID: 'all',
      altListData: [],
      moment: moment,
      project: '',
      case: '',
      ModelList: [],
      listLoading: false,
      filters: {
        name: ''
      },
      total: 0,
      page: 1,
      sels: [], // 列表选中列
      activeIndex: ''
    }
  },
  methods: {
    // 获取项目列表
    getModelsList () {
      this.listLoading = true
      let self = this
      let params = { page: self.page,
        user_key: JSON.parse(sessionStorage.getItem('name')),
        name: self.filters.name }
      // let headers = {Authorization: 'Token '+JSON.parse(sessionStorage.getItem('token'))};
      let headers = {
        'Content-Type': 'application/json',
        Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
      }
      getRegisteredModels(headers, params).then((res) => {
        self.listLoading = false
        let { msg, code, data } = res
        if (code === '999999') {
          self.total = data.total
          self.models = data.data
          self.model_meta = data.model_meta
          console.log(self.models, 'self.models')
          console.log(self.total, 'self.total')
          console.log(self.model_meta, 'self.model_meta')
        } else {
          self.$message.error({
            message: msg,
            center: true
          })
        }
      })
    },
    querySearchAsync (queryString, cb) {
      const models = this.models.map(item => ({ value: item.name, label: item.name }))
      const results = queryString ? models.filter(this.createStateFilter(queryString)) : models

      clearTimeout(this.timeout)
      this.timeout = setTimeout(() => {
        cb(results)
      }, 300)
    },
    createStateFilter (queryString) {
      return (state) => {
        return (state.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0)
      }
    },
    handleSelect (item) {
      console.log(item)
    },
    // 新增
    submitSave: function () {
      let self = this
      // NProgress.start();
      let params = JSON.stringify({
        feature_id: self.$route.params.feature_id,
        description: self.editFeatureMeta.description,
        user_name: JSON.parse(sessionStorage.getItem('name'))
      })
      console.log(params, 'params')
      let header = {
        'Content-Type': 'application/json',
        Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
      }
      EditFeatureDesc(header, params).then(_data => {
        let { msg, code } = _data
        self.addLoading = false
        if (code === '999999') {
          self.$message({
            message: '修改成功',
            center: true,
            type: 'success'
          })
          this.descEditable = false
          this.descvisalble = true
          this.editFeatureMeta.description = ''
          self.getFeatureMata()
        } else if (code === '999997') {
          self.$message.error({
            message: msg,
            center: true
          })
        } else {
          self.$message.error({
            message: msg,
            center: true
          })
          this.editFeatureMeta.description = ''
          this.descEditable = false
          this.descvisalble = true
          self.getFeatureMata()
        }
      })
    },
    handelCancel: function () {
      this.descEditable = false
      this.editCampaignMeta.description = ''
      this.descvisalble = true
    },
    handleEdit: function () {
      this.descEditable = true
      this.editFeatureMeta.description = this.featureMeta.description
      this.descvisalble = false
    },
    handleSubModelEdit: function (index, row) {
      this.editFormVisible = true
      this.editForm = Object.assign({}, row)
    },
    // 获取活动的元数据
    getFeatureMata () {
      let self = this
      let param = { feature_id: self.$route.params.feature_id }
      $.ajax({
        type: 'get',
        url: test + '/api/features/get_feature_metadata',
        async: true,
        data: param,
        headers: {
          Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
        },
        timeout: 5000,
        success: function (data) {
          if (data.code === '999999') {
            self.featureMeta = data.data.data
            console.log(self.featureMeta, 'self.featureMeta')
          } else {
            self.$message.error({
              message: data.msg,
              center: true
            })
          }
        }
      })
    },
    // 显示新增界面
    handleAdd: function () {
      this.addFormVisible = true
    },
    // 编辑
    editSubmit: function () {
      let self = this
      this.$refs.editForm.validate((valid) => {
        if (valid) {
          this.$confirm('确认提交吗？', '提示', {}).then(() => {
            self.editLoading = true
            // NProgress.start();
            let params = {
              name: self.editForm.name,
              version: self.editForm.version,
              status: self.editForm.status,
              description: self.editForm.description,
              user_name: JSON.parse(sessionStorage.getItem('name'))
            }
            let header = {
              'Content-Type': 'application/json',
              Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
            }
            updateFeatureModel(header, params).then(_data => {
              let { msg, code } = _data
              self.editLoading = false
              if (code === '999999') {
                self.$message({
                  message: '修改成功',
                  center: true,
                  type: 'success'
                })
                self.$refs['editForm'].resetFields()
                self.editFormVisible = false
                self.getFeatureModelList()
              } else if (code === '999997') {
                self.$message.error({
                  message: msg,
                  center: true
                })
              } else {
                self.$message.error({
                  message: msg,
                  center: true
                })
              }
            })
          })
        }
      })
    },
    // 新增
    addSubmit: function () {
      this.$refs.addForm.validate((valid) => {
        if (valid) {
          let self = this
          this.$confirm('确认提交吗？', '提示', {}).then(() => {
            self.addLoading = true
            // NProgress.start();
            let params = JSON.stringify({
              name: self.addForm.name,
              version: self.addForm.version,
              feature_id: self.$route.params.feature_id,
              description: self.addForm.description,
              user_name: JSON.parse(sessionStorage.getItem('name'))
            })
            let header = {
              'Content-Type': 'application/json',
              Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
            }
            addFeatureModel(header, params).then(_data => {
              let { msg, code } = _data
              self.addLoading = false
              if (code === '999999') {
                self.$message({
                  message: '添加成功',
                  center: true,
                  type: 'success'
                })
                self.$refs['addForm'].resetFields()
                self.addFormVisible = false
                self.getFeatureModelList()
              } else if (code === '999997') {
                self.$message.error({
                  message: msg,
                  center: true
                })
              } else {
                self.$message.error({
                  message: msg,
                  center: true
                })
                self.$refs['addForm'].resetFields()
                self.addFormVisible = false
                self.getFeatureModelList()
              }
            })
          })
        }
      })
    },
    handleChange (val) {
      console.log(val)
    },
    LookExpDetails (index, row) {
      let self = this
      self.$router.push({ name: '查看实验详情',
        params: { api_id: row.id } })
    },
    expandModelClick (val) {
      this.getExpAltList(val.id)
      // console.log("expandModelClick",this.altListData,val.id)
    },
    getFeatureModelList () {
      this.listLoading = true
      let self = this
      $.ajax({
        type: 'get',
        url: test + '/api/features/get_feature_models',
        async: true,
        data: { feature_id: this.$route.params.feature_id,
          page: self.page,
          name: self.filters.name,
          user_name: JSON.parse(sessionStorage.getItem('name'))
        },
        headers: {
          Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
        },
        timeout: 5000,
        success: function (data) {
          self.listLoading = false
          if (data.code === '999999') {
            self.ModelList = data.data.data
            self.total = data.data.total
            /* data.data.data.forEach((item) => {
              item.result = false
              self.ModelList.push(item)
            }) */
            console.log(self.ModelList, 'self.Modellist')
            // self.ApiList = data.data.data
          } else {
            self.$message.error({
              message: data.msg,
              center: true
            })
          }
        }
      })
    },
    handleDel (index, row) {
      this.$confirm('确认删除该模型信息吗?', '提示', {
        type: 'warning'
      }).then(() => {
        this.listLoading = true
        // NProgress.start();
        let self = this
        $.ajax({
          type: 'post',
          url: test + '/api/features/del_feature_model',
          async: true,
          data: JSON.stringify({
            model_id: row.id,
            user_name: JSON.parse(sessionStorage.getItem('name')) }),
          headers: {
            'Content-Type': 'application/json',
            Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
          },
          timeout: 5000,
          success: function (data) {
            if (data.code === '999999') {
              self.$message({
                message: '删除成功',
                center: true,
                type: 'success'
              })
            } else {
              self.$message.error({
                message: data.msg,
                center: true
              })
            }
            self.getFeatureModelList()
          }
        })
      }).catch(() => {
      })
    },
    // 获取实验候选
    getExpAltList (expId) {
      let self = this
      $.ajax({
        type: 'get',
        url: test + '/api/campaigns/case_exp_altlist',
        async: true,
        data: { campaign_id: this.$route.params.campaign_id,
          ab_id: expId },
        headers: {
          Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
        },
        timeout: 5000,
        success: function (data) {
          if (data.code === '999999') {
            self.altListData = data.data.data
          } else {
            self.$message.error({
              message: data.msg,
              center: true
            })
          }
        }
      })
    },
    handleCurrentChange (val) {
      this.page = val
      this.getFeatureModelList()
    }
  },
  mounted () {
    this.getFeatureMata()
    this.getFeatureModelList()
    this.getModelsList()
    // this.getCampaignMata()
    // this.project = this.$route.params.project_id
    // this.case = this.$route.params.case_id
    // if (this.$route.params.firstID) {
    //   this.firstID = this.$route.params.firstID
    // } else {
    //   this.firstID = 'all'
    //  }
    // this.firstID = this.$route.params.firstGroup
    // console.log(this.firstID, 'this.$route.params.project_id')
  }
}
</script>

  <style lang="scss" scoped>
    p{
        margin: 0 0 10px;
    }
    .return-list {
        margin-top: 0px;
        margin-left: 10px;
        margin-right: 10px;
        margin-bottom: 10px;
        border-radius: 0px;
    }
    .HttpStatus {
        border-radius: 25px;
        padding: 10px;
        box-sizing: border-box;
        color: #fff;
        font-size: 15px;
        background-color: #409eff;
        text-align: center;
        margin-right: 10px;
    }
    .resultStyle{
        margin-left: 2%;
        margin-top: 10px;
    }
    .lin{
        left: 2%;
        border: 1px solid #e6e6e6;
        width: 90%;
        position: relative
    }
  </style>
